/** @jsx React.DOM *//** @jsx React.DOM **/

var React   = require('react');

var groupby = require('lodash.groupby');
var sortby  = require('lodash.sortby');

var beasts = groupby(require('../db/animals.json'), function(d) {
  return d.challenge
});

var App = React.createClass({displayName: 'App',

  getAnimal:function(animal, i) {
    return (
      React.DOM.span({key: i },  i > 0 ? ', ' : '',  animal.name)
    );
  },

  getAnimals:function(animals, i) {
    return (
      React.DOM.p({className: "block col-2", key: i },  sortby(animals, 'name').map(this.getAnimal) )
    )
  },

  render:function() {
    var collection = Object.keys(beasts).sort(function(a, b) {
      return parseFloat(a) > parseFloat(b) ? 1 : -1;
    }).map(function(b) {
      return (
        React.DOM.div(null, 
          React.DOM.h3({className: "col-1"}, " ", b ), 
           this.getAnimals(beasts[b]) 
        )
      );
    }, this);

    return (
      React.DOM.div(null, 
        React.DOM.h2(null, "Beastiary"), 
        collection 
      )
    );
  }
});

React.renderComponent(App(null), document.body);



